.KeyboardLetter {
    display: inline-block;
    /* padding: 20px 15px; */
    flex-grow: 1;
    /* width: 15px; */
    height: 50px;
    line-height: 50px;
    margin: 5px;
    background-color: rgb(112, 112, 112);
    border-radius: 5px;
    min-width: 25px;
    cursor: pointer;
}

.notfound {
    background-color: rgb(71, 71, 71) !important;
}

.match {
    background-color: rgb(67, 190, 73);
    border-color: rgb(67, 190, 73);
}

.found {
    background-color: rgb(197, 195, 80);
    border-color: rgb(197, 195, 80);
}

